* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Raleway", sans-serif;
  -webkit-tap-highlight-color: transparent;
}

body, #root{
  width: 100%;
  overflow: hidden;
  height: 100vh;
}

button * {
  pointer-events: none;
}

.container {
  width: 100%;
  height: calc(100vh - 54px);
  margin: 0 auto;
  max-width: 500px;
  display: flex;
  justify-content: start;
  align-items: flex-start;
  flex-direction: column;
  overflow: hidden auto;
  scroll-snap-type: y mandatory;
}

.video {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  scroll-snap-align: start;
  scroll-snap-stop: always;
  position: relative;
  overflow: hidden;
}

.video__video {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #000;
}

.video__video > video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 5;
}

.video__controls {
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: end;
  align-items: flex-end;
  gap: 20px;
  padding: 10px;
}

.video__controls > div {
  pointer-events: all;
}

.video__controls > .video__user {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 5px;
  align-items: flex-start;
  justify-content: end;
  color: #fff;
}

.video__controls > .video__btns {
  padding: 10px 10px 0;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  gap: 15px;
  align-items: center;
  justify-content: end;
}

.video__controls > .video__btns > .video__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 5px;
}

.video__controls > .video__btns > .video__btn > button {
  cursor: pointer;
  width: 45px;
  height: 45px;
  font-size: 25px;
  border: none;
  outline: none;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.1s;
}

.video__controls > .video__btns > .video__btn > button:hover {
  filter: brightness(0.9);
}

.video__controls > .video__btns > .video__btn > button:active {
  filter: brightness(0.8);
  transform: scale(0.95);
}

.video__controls > .video__btns > .video__btn > span {
  font-size: 13px;
  color: #fff;
}

/* like btn style */
.video__controls > .video__btns > .video__btn > button.likedBtn {
  background: pink;
  color: red;
}

.videoPausedIcon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  font-size: 60px;
  z-index: 200;
  pointer-events: none;
}


/* NAVBAR */
.navbar{
  width: 100%;
  padding: 15px;
  background: #131313;
}

.navbar > ul{
  list-style: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: 10px;
}

.navbar > ul > li > svg{
  font-size: 20px;
  color: #fff;
}